@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
}

#myVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

.welcomeHeader {
    text-align: center;
    position: relative;
    top: 18%;
}

.welcomeTime {
    font-size: 4em;
    font-weight: bold;
}

.dateLabel {
    font-size: 1.7em;
    padding-top: 1%;
}

.linksParentContainer {
    position: relative;
    top: 30%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 9%;
    grid-row-gap: 0px;
    padding-left: 10%;
    padding-right: 10%;
}

.work {
    grid-area: 1 / 1 / 2 / 2;
}

.collegeApps {
    grid-area: 1 / 2 / 2 / 3;
}

.messaging {
    grid-area: 1 / 3 / 2 / 4;
}

.socialMedia {
    grid-area: 1 / 4 / 2 / 5;
}

.links {
    list-style-type: none;
    margin: 0;
    padding-left: 10px;
}

li {
    font-size: 1.1em;
}

li:not(:last-child) {
    margin-bottom: 5px;
}

.groupHeader {
    font-size: 1.3em;
    font-weight: bold;
}

.orangeHeader {
    background-image: -webkit-gradient(linear, left top, right top, from(#fc4a1a), to(#f7b733));
    background-image: linear-gradient(to right, #fc4a1a, #f7b733);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.blueHeader {
    background-image: -webkit-gradient(linear, left top, right top, from(#0072ff), to(#00c6ff));
    background-image: linear-gradient(to right, #0072ff, #00c6ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.greenHeader {
    background-image: -webkit-gradient(linear, left top, right top, from(#11998e), to(#38ef7d));
    background-image: linear-gradient(to right, #11998e, #38ef7d);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.redHeader {
    background-image: -webkit-gradient(linear, left top, right top, from(#DD2476), to(#FF512F));
    background-image: linear-gradient(to right, #DD2476, #FF512F);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

hr {
    height: 3px;
    border: none;
}

.orangeLine {
    background: -webkit-gradient(linear, left top, right top, from(#fc4a1a), to(#f7b733));
    background: linear-gradient(to right, #fc4a1a, #f7b733);
}

.blueLine {
    background: -webkit-gradient(linear, left top, right top, from(#0072ff), to(#00c6ff));
    background: linear-gradient(to right, #0072ff, #00c6ff);
}

.greenLine {
    background: -webkit-gradient(linear, left top, right top, from(#11998e), to(#38ef7d));
    background: linear-gradient(to right, #11998e, #38ef7d);
}

.redLine {
    background: -webkit-gradient(linear, left top, right top, from(#DD2476), to(#FF512F));
    background: linear-gradient(to right, #DD2476, #FF512F);
}

a {
    color: black;
}

a:link {
    text-decoration: none;
}

a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

a:active {
    text-decoration: underline;
}
